.mail{
    flex: 1;
    font-size: 400;
    background-color: white;
}

.mail__tools{
    display: flex;
    justify-content: space-between;
    background-color: whitesmoke;
}
.mail__toolsLeft{
    display: flex;
}

.mail__body{
    display: flex;
    flex-direction: column;
    margin: 30px;
    background-color: white;
    padding: 20px;
    height: 100vh;
    box-shadow: 0px 5px 0px rgb(0, 0, 0, 0.24);

}
.mail__bodyHeader{
    display: flex;
    align-items: center;
    border-bottom: 1px solid whitesmoke;
    padding: 20px;
    position: relative;
}

.mail__bodyHeader > h2{
    font-weight: 400;
    margin-right: 20px;
}

.mail__import{
    color: #e8ab02 !important;
}

.mail__bodyHeader > p {
   margin-left: 20px;

}

.mail__message > p {
    padding: 10px;
    margin-right: 20px;
    overflow: break-word;
}

.mail__time {
    position: absolute;
    top: 24px;
    right: 0;
    font-size: 18px;
    color: gray;
}