.section {
    display: flex;
    align-items: center;
    padding-left: 10px;
    border-bottom: 2px;
    padding: 15px;
    min-width: 200px;
    cursor: pointer;

}
.section 

.section--selected {
    background-color: whitesmoke;
    border-radius: 3px !important;


}

.section:hover{
    background-color: whitesmoke;
    border-width: 3px !important;
}

.section > h4{
    font-size: 14px;
    margin-left: 15px;
}